.auction-room {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.auction-item {
    text-align: center;
    margin-bottom: 20px;
}

.item-image img {
    max-width: 300px;
    max-height: 300px;
    object-fit: cover;
    border: 1px solid #ccc;
    margin-bottom: 10px;
}

.current-price, .end-time {
    font-size: 1.2em;
    font-weight: bold;
    margin: 10px 0;
}

.bidding-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.bidding-section input {
    width: 200px;
    padding: 10px;
    margin: 10px 0;
    font-size: 1.1em;
    text-align: center;
}

.bidding-section button {
    padding: 10px 20px;
    font-size: 1.1em;
    background-color: #28a745;
    color: #fff;
    border: none;
    cursor: pointer;
}

.bidding-section button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.feedback-section {
    width: 80%;
    max-width: 600px;
    margin-top: 20px;
}

.feedback-message {
    background-color: #f8f9fa;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 5px;
    border-radius: 4px;
}
